.copyright{
    position: absolute;
    bottom: 0px;
    font-size: 15px;
    color: #979797;
    text-align: center;
    margin-bottom: 5px;
}

.profilebarBackground{
    position: sticky;
    top: 0;
    background-color: #FFFFFF;
    width: 320px;
    height: 100vh;
}

.profilebar{
    position: relative;
    background-color: #FFFFFF;
    width: 92%;
    height: 100%;
    box-shadow: 5px 0px 12px 6px rgba(0, 0, 0, 0.46);
    
}

.userprofile{
    margin-top: 20px;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-bottom: -30px;
}
.userprofile .text{
    font-weight: bold;
    font-size: 30px;
    position: relative;
    top: -20px;
    
}
.userprofile .icon:hover{
    position: relative;
    top : -40px;
    margin: 6px;
    background-color: #979797;
    padding: 10px;
    border-radius: 50%;
    cursor: pointer;
    transition:  0.3s;
    
}
.userprofile .icon{
    position: relative;
    top : -40px;
    margin: 6px;
    background-color: #E7E7E7;
    padding: 10px;
    border-radius: 50%;
    cursor: pointer;
    transition:  0.3s;
}

.userprofile .icon img{
    
    position: relative;
    height: 20px;
    width: 20px;
    top: 3px;
    
}
.logo{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 60%;
}

.profilebuttonON{
    display: flex;
    height: 54px;
    background-color: #979797;
    border-radius: 20px;
    margin: 30px 30px 30px 30px;
    box-shadow: 0 5px 3px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    cursor: pointer;
    text-decoration: none;
}

.profilebutton {  
    text-decoration: none;
    display: flex;
    height: 54px;
    background-color: #E7E7E7;
    border-radius: 20px;
    margin: 30px 30px 30px 30px;
    box-shadow: 0 5px 3px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}
.profilebutton:hover{
    display: flex;
    height: 54px;
    background-color: #979797;
    border-radius: 20px;
    padding-left: 10%;
    margin: 30px 30px 30px 30px;
    cursor: pointer;
    box-shadow: 0 5px 3px rgba(0, 0, 0, 0.2);  
}

.content{
    display: flex;
    color: #050505;
    align-items: center;
    
}
.content .text{
    font-size: 20px;
    width: 200px;
    justify-content: left;
    padding-left: 10%;
}

.profilebuttonImg {
    max-height: 30px;
    margin: 0 5px 0 30px;
}

.barhr{
    margin: 10px 10px 10px 10px;
    opacity: 0.3;
}
