.Mainbox {
  width: 100%;
  margin: 50px 0px 20px 50px;
}
.Topicbox {
  width: fit-content;
  background-color: #ffffff;
  padding: 18px;
  border-radius: 20px;
  font-size: 30px;
}

.Topicbox .Text {
  cursor: pointer;
}
.Topicbox .Text:hover {
  text-decoration: underline;
  cursor: pointer;
}

.Postbox {
  position: relative;
  background-color: #ffffff;
  padding: 18px 30px 18px 30px;
  margin-top: 20px;
  margin-right: 50px;
  border-radius: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.Postbox .Title {
  overflow-wrap: break-word;
  font-size: 30px;
}


.tag {
  display: inline;
  background-color: #d9d9d9;
  color: #353535;
  width: fit-content;
  margin: 0 10px 0 0;
  padding: 3px 15px 3px 15px;
  border-radius: 20px;
  font-size: 16px;
}

.detail {
  color: #3d3d3d;
}
.transparent {
  position: absolute;
  bottom: 50px;
  left: 0;
  width: 100%;
  height: 50px; /* Adjust the height of the transparent part as needed */
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1)
  );
  pointer-events: none;
}

.CLV {
  position: absolute;
  right: 30px;
  bottom: 15px;
  display: inline;
}

.CLV .comment {
  margin: 0 5px 0 10px;
  position: relative;
  top: 4px;
  width: 25px;
  /* cursor: pointer; */
}
.CLV .heart {
  margin: 0 5px 0 5px;
  /* cursor: pointer; */
  width: 25px;
}
.CLV .text {
  color: #afafaf;
  font-size: 18px;
  overflow-wrap: break-word;
}
.CLV .verify {
  margin: 0 5px 0 5px;
  position: relative;
  top: 5px;
  width: 28px;
}

.viewButton {
  margin-left: 10px;
  position: relative;
  bottom: 5px;
  cursor: pointer;
  font-size: 20px;
  color: #7b7b7b;
  background-color: #d9d9d9;
  padding: 6px 23px 6px 23px;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 4px;
  text-decoration: none;
}
.CreatePostButton {
  display: block;
  margin-bottom: 18px;
  font-size: 30px;
  position: relative;
  color: #000000;
  text-decoration: none;
  cursor: pointer;
  font-size: 20px;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 6px 4px;
  width: fit-content;
  transition: 0.3s;
}

.CreatePostButton:active {
  margin-bottom: 18px;
  font-size: 30px;
  position: relative;
  cursor: pointer;
  font-size: 20px;
  background-color: #979797;
  padding: 20px;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 6px 4px;
  width: fit-content;
}

.CreatePostButton .icon {
  position: relative;
  top: 2px;
  width: 50px;
  color: #575757;
}
.CreatePostButton .text {
  position: relative;
  top: -5px;
  margin: auto;
  width: 50%;
  font-size: 40px;
  padding: 10px;
}


.CreatePostButtonContainer {
  text-align: right;
  margin-top: 50px;
}

/* HTML: <div class="loader"></div> */
.loader {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  width: 88px;
  height: 12px;
  --_g: no-repeat radial-gradient(farthest-side,#000 94%,#0000);
  background:
    var(--_g) 25% 0,
    var(--_g) 75% 0;
  background-size: 12px 12px;
  position: relative;
  animation: l24-0 1s linear infinite;
}
.loader:before {
  content: "";
  position: absolute;
  height: 12px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #000;
  inset: 0;
  margin: auto;
  animation: l24-1 1s cubic-bezier(0.5,300,0.5,-300) infinite;
}
@keyframes l24-0 {
  0%,24%  {background-position: 25% 0,75% 0}
  40%     {background-position: 25% 0,85% 0}
  50%,72% {background-position: 25% 0,75% 0}
  90%     {background-position: 15% 0,75% 0}
  100%    {background-position: 25% 0,75% 0}
}
@keyframes l24-1 {
  100% {transform:translate(0.1px)}
}